.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.flexBorder {
    display: flex;
    /* justify-content : center;  */
    flex: "1";
    flex-direction: column;
    margin-right: 50px;
    margin-left: -200px;
}

.buttonContainer {
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
}

@media screen and (max-width: 700px) {
    .flexBorder {
        display: block;

    }
}

.container {
    margin: 30px
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.fa-trash,
.fa-edit {
    cursor: pointer;
}


/* generic flex container by column */

.flexContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 0.5;
    border: 0px !important;
    width: 100%;
}

.formInput {
    margin-top: 15px
}

.flexRow {
    display: flex;
    flex-direction: row;
    height: 100%;
}

.menuSection {
    height: 100vh;
    border-right: 1px solid rgba(0, 0, 0, 0.12);
}